@import url(https://fonts.googleapis.com/css2?family=Poppins);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
    outline: none !important;
    max-width: 100%;
    scroll-behavior: smooth;
}

.body {
    font-family: 'Poppins', sans-serif;
}

p {
    font-size: 1.15rem;
}

.header {
    z-index: 1000000;
    position: relative;
}

.navbar {
    background-color: transparent !important;
    transition: 0.5s;
}

.scrolled {
    background: rgb(39, 164, 59) !important;
    transition: 0.5s;

}

.navbar-light .navbar-nav .active > .nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show > .nav-link {
    color: #000000;
    background: #ffffff;
}

.navbar-light .navbar-nav .nav-link {
    color: rgba(255, 255, 255, .8);
}

.navbar-light .navbar-brand,
.navbar-light .navbar-brand:hover,
.navbar-light .navbar-brand:focus,
.navbar-light .navbar-brand:visited {
    color: rgba(255, 255, 255, 1);
    font-size: 2.5rem;
    font-weight: 600;
}

.navbar-light .navbar-nav .nav-link:hover,
.navbar-light .navbar-nav .nav-link:focus {
    color: rgba(255, 255, 255, 1);
}

.navbar-light .navbar-nav .active > .nav-link:hover {
    color: black !important;
}

.banner {
    position: relative;
    top: 0;
    width: 100%;
    height: 100vh !important;
    background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/plantTree/static/media/banner.6d619a25.jpg);
    background-size: cover !important;
}

.banner:before,
.sec2:before,
.contact:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 400px;
    background: linear-gradient(#27a43b, transparent, transparent, transparent, transparent, transparent);
    pointer-events: none;
}

.banner:after,
.sec2:after,
.contact:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 400px;
    background: linear-gradient(360deg, #27a43b, transparent, transparent, transparent, transparent, transparent);
    pointer-events: none;
}

.height100p {
    height: 100vh;
}

.h100 {
    height: 100%;
}

.contentBox {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 10;
}

.contentBox h1 {
    font-weight: 800;
    color: #ffffff;
    text-transform: uppercase;
    font-size: 5rem;
}

.contentBox p {
    color: #ffffff;
    font-size: 1.3rem;
}

.sec1 {
    padding: 100px 0;
    background: #27a43b;
}

.headerText {
    color: #fff;
}

.headerText h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.placeBox {
    position: relative;
    max-width: 300px;
    height: 400px;
    margin: 0 auto;
    background: #27a43b;
    margin-top: 30px;
}

.placeBox .imgBx {
    width: 100%;
    height: 100%;
}

.placeBox .imgBx img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.jumbotron {
    margin-bottom: 0 !important;
}

.placeBox .content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: left;
    align-items: flex-end;
}
.row{
    margin-left: 0;
    margin-right: 0;
}
.placeBox .content h3 {
    position: relative;
    margin: 0;
    padding: 10px;
    background: rgba(39, 164, 59, .95);
    color: #fff;
    font-size: 20px;
    font-weight: 600;
    width: 100%;
    text-align: center;
}

.placeBox .content h3 span {
    font-size: 16px;
    font-weight: 500;
}

.sec2 {
    position: relative;
    background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/plantTree/static/media/2.2d5fdf8d.jpg);
    background-size: cover;
    height: 110vh;
}
.navbar-light .navbar-toggler {
        border-radius: 20px;
    }
.nav-link.active,
.nav-link.active:hover{
    color: #000000 !important;
}
.sec2 h2 {
    font-weight: 800;
    color: white;
    text-transform: uppercase;
    font-size: 4.5rem;
}

.btnD1 {
    border: 2px solid #fff;
    color: #000;
    background: #fff;
    border-radius: 50px;
    padding: 15px 50px;
    font-size: 1.15rem;
    outline: none;
    font-weight: 500;
    margin-top: 20px;
}

.blog {
    position: relative;
    padding: 100px 0;
    background: #27a43b;
}

.blogpost {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 10px;
    object-fit: cover;
}

.blogpost .content {
    padding: 15px;
    background: white;
}

.blogpost .content h1 {
    margin-top: 20px;
    padding: 0 0 10px;
    font-weight: 600;
    font-size: 1.5rem;
}

.btnD2,
.btnD2:hover {
    background: #1e8d34;
    color: #fff;
    border-radius: 30px;
    padding: 10px 20px;
    float: right;
}

.contact {
    position: relative;
    padding: 100px 0;
    background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/plantTree/static/media/contact.4987a9cf.jpg);
    background-size: cover;
    min-height: 100vh;

}

.contact .form-control {
    border: none;
    border-bottom: 2px solid rgba(255, 255, 255, .2);
    height: 50px;
    border-radius: 0;
    background: rgba(0, 0, 0, 0.4);
    color: #ffffff;
    font-size: 1.25rem;
}

.contact .form-control:focus,
.contact .form-control:hover {
    border: none;
    border-bottom: 2px solid rgba(255, 255, 255, 1);
    box-shadow: none;
}

.contact .textarea {
    min-height: 100px;
}

.contact form {
    position: relative;
    z-index: 1000;
}

.contact label {
    color: #ffffff;
    font-size: 1.25rem;
}

footer {
    background: #2bb542;
    padding: 50px 0 0;
}

.sci ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sci li {
    list-style: none;
    margin: 0 20px;
}

.sci li a {
    color: #ffffff;
    font-size: 2em;
    transition: 0.5s;
}

.sci li a:hover {
    color: #fff;
}

.cpryt {
    color: #ffffff;
    text-align: center;
    margin-top: 20px;
}

@media (max-width: 992px) {
    .navbar-light .navbar-toggler {
        background: #fff;
        border-radius: 20px;
    }

    .banner {
        min-height: 100vh;
        padding: 100px 0;
    }

    .contentBox h1,
    .sec2 h2 {
        font-size: 2rem !important;
    }

    .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: 1rem;
        padding-left: 1rem;
    }

    .sec1 {
        padding: 20px 0;
    }

    .headerText {
        font-size: 2rem;
    }

    .blog {
        padding: 20px 0;
    }

    .blogpost .imgBx {
        height: 200px;
    }

    .contact {
        padding: 20px 0;
    }

    .contact .form-control {
        height: 40px;
        border: 1px solid #fff;
    }

    .btnD1 {
        padding: 10px 40px;
        margin-top: 10px;
    }

    .footer {
        background: #27a43b;
        padding: 0 0 20px;
    }
    li{
        margin: 0 10px;
    }
    li a{
        font-size: 1.5em;
    }
    .sec2{
        height: auto;
        padding: 40px 0;
    }

}

